<template>
    <div>
      <PersonalHeader></PersonalHeader>
      <div class="evaBox">
        <div class="evaContent">
          <h3>智能评估</h3>
          <div class="line">
            <div class="active"><span>1</span></div>
            <div :class="[step > 1 ? 'active':'']"><span>2</span></div>
            <div :class="[step > 2 ? 'active':'']"><span>3</span></div>
          </div>
          <div class="part1" :hidden="step != 1">
            <h4 class="evaTitle">请填写您的基本信息</h4>
            <div class="row clearfix">
              <label class="name">您的姓名</label>
              <input class="textInp" v-model="form.name" type="text" placeholder="请填写您的姓名" value="">
            </div>
            <div class="row clearfix">
              <label class="name">加盟区域</label>
              <div class="selectBox">
                <el-select v-model="province_id" placeholder="请选择省份" @change="getCity">
                  <el-option v-for="item in provinceList" :key="'province'+item.id" :label="item.cityname" :value="item.id">
                    <span >{{ item.cityname }}</span>
                  </el-option>
                </el-select>
              </div>
              <span class="fl unit provice">省</span>
              <div class="selectBox">
                <el-select v-model="form.city_id" placeholder="请选择城市">
                  <el-option v-for="item in cityList" :key="'city'+item.id" :label="item.cityname" :value="item.id">
                    <span >{{ item.cityname }}</span>
                  </el-option>
                </el-select>
              </div>
              <span class="fl unit">市</span>
            </div>
            <div class="row clearfix">
              <label class="name">个人资源</label>
              <input class="textInp" type="text" placeholder="请填写您的个人资源" value="">
            </div>
          </div>
          <div class="part2" :hidden="step != 2">
            <h4 class="evaTitle">请选择您从事的行业</h4>

            <div class="choiceBox">
              <el-checkbox-group v-model="currentCate" size="medium">
                <el-checkbox-button  v-for="(item,index) in cateList" :label="item.id" :key="'current'+item.id"> {{item.name}}</el-checkbox-button>
              </el-checkbox-group>
            </div>
            <h4 class="evaTitle">请填写您的预算金额</h4>
            <div class="budget clearfix">
              <label class="name">预算金额</label>
              <div class="border clearfix">
                <input class="fl" type="text" v-model="form.money" placeholder="请输入您的预算金额">
                <span class="fr">万元</span>
              </div>
            </div>
          </div>
          <div class="part3" :hidden="step != 3">
            <h4 class="evaTitle">请选择您的意向行业</h4>
            <div class="choiceBox">
              <el-checkbox-group v-model="likeCate" size="medium">
                <el-checkbox-button  v-for="(item,index) in cateList" :label="item.id" :key="'like'+item.id"> {{item.name}}</el-checkbox-button>
              </el-checkbox-group>
            </div>
            <h4 class="evaTitle">请填写您的联系方式</h4>
            <div class="budget clearfix">
              <label class="name">手机号码</label>
              <div class="border clearfix">
                <input class="fl" type="text" v-model="form.phone" placeholder="请输入您的手机号码">
              </div>
            </div>
            <div class="budget clearfix">
              <label class="name">图片验证</label>
              <div class="border clearfix">
                <input class="fl testInp" type="text" v-model="form.img_code" placeholder="请输入图片验证码">
                <img class="imgCode" @click="getImgCode" :src="imgCode">
              </div>
            </div>
            <div class="budget clearfix">
              <label class="name">手机验证</label>
              <div class="border clearfix">
                <input class="fl testInp" type="text" v-model="form.code" placeholder="请输入手机验证码">
                <span class="getBtn" v-if="showCode" @click="getCode">获取验证码</span>
                <span class="getBtn" v-else>{{outTime}}S后重新获取</span>
              </div>
            </div>
          </div>
          <div class="btns">
            <el-button type="info" @click="upPage" :style="{'display': step == 1 ? 'none':'inline-block'}" >上一步</el-button>
            <el-button  type="primary"  @click="nextPage"  :style="{'display': step == 3 ? 'none':'inline-block'}">下一步</el-button>
            <el-button type="primary" @click="submitForm" :style="{'display': step != 3 ? 'none':'inline-block'}">完成</el-button>
          </div>


        </div>
      </div>
    </div>
</template>

<script>
    import PersonalHeader from '@/components/PersonalHeader'
    import api from '@/api/evaluate-api'
    import commonApi from '@/api/common-api'
    export default {
      name: "index.vue",
      data() {
        return {
          step:1,
          checkboxGroup: [],
          form:{},
          cateList:[],
          outTime:60,
          provinceList:[],
          cityList: [],
          imgCode:'',
          province_id:'',
          likeCate:[],
          currentCate:[],
          value: '',
          showCode: true,
        }
      },
      created() {
        this.getImgCode();
        this.getProvince();
        this.getCate();
      },
      methods:{
        /**
         * 获取行业分类
         */
        getCate: function(){
            api.getCate({id:0}).then(data=>{
              this.cateList = data;
            })
        },
        /**
         * 获取短信验证码
         */
        getImgCode:function(){
          commonApi.getImgCode().then(data => {
            this.imgCode = data
          });
        },

        /**
         * 获取验证码
         */
        getCode: function() {
          console.log("this.form;",this.form);
          const {phone = '',img_code=''} = this.form;
          const _this = this;
          if (!this.validatePhone(phone)) {
            return
          }
          this.showCode = false;
          let intervalID = setInterval(function() {
            if (_this.outTime == 0) {
              _this.showCode = true;
              _this.outTime = 60;
              clearInterval(intervalID)
            }
            _this.outTime--;
          }, 1000)
          commonApi.getPhoneCode({phone,img_code}).then(data => {
            this.$message({message:'验证码获取成功',type: 'success'});
          });
        },

        /**
         * 验证手机格式
         * @param {Object} phone
         */
        validatePhone: function(phone) {
          if (!phone) {
            this.$message.error('请输入手机号码');
            return false;
          }
          if (phone.length != 11) {
            this.$message.error('手机号码格式不正确');
            return false;
          }
          return true;
        },

        invalidateForm:function(){
          const {likeCate,currentCate,form:{name,city_id,money,phone,resources,code,img_code}} = this;
          if (!name ) {
            this.$message.error('请填写您的姓名');
            return false;
          }
          if (!city_id ) {
            this.$message.error('请选择加盟区域');
            return false;
          }
          if (currentCate.length == 0 ) {
            this.$message.error('请至少选择一个您从事的行业');
            return false;
          }
          if (!money ) {
            this.$message.error('请填写您的预算金额');
            return false;
          }
          if (likeCate.length == 0 ) {
            this.$message.error('请至少选择一个意向行业');
            return false;
          }
          if (!name ) {
            this.$message.error('请填写您的姓名');
            return false;
          }
          if (!phone ) {
            this.$message.error('请填写您的手机号码');
            return false;
          }
          if (!img_code ) {
            this.$message.error('请填写图片验证码');
            return false;
          }
          if (!code ) {
            this.$message.error('请填写您的手机验证码');
            return false;
          }
          return true
        },
        getProvince: function(){
          api.getCity({pid:0}).then(data=>{
            this.provinceList = data;
          });
        },
        getCity: function(){
          // this.form.city_id = null;
          api.getCity({pid:this.province_id}).then(data=>{
            this.cityList = data;
          });
        },
        upPage:function (){
          this.step --
        },
        nextPage: function(){
          this.step ++
        },
        submitForm: function(){
            console.log("form",);
            if(this.invalidateForm()){
              const {likeCate,currentCate,form} = this;
              api.submitForm({like_cate_id:likeCate.join(','),cate_id:currentCate.join(','),...form}).then(data=>{
                  this.$message.success('提交成功')
                setTimeout(()=>{
                  this.$router.push('/evaluationReport?id='+data);
                },800);
              });
            }

        }
      },
      components:{
        PersonalHeader,
      }
    }
</script>

<style lang="less" type="text/less" scoped>
  .evaBox {
    width: 100%;
    padding: 70px 0;
    height: auto;
    background: url("../../../static/image/evaluateBg.png") center top no-repeat #F4F5FA;

    .evaContent {
      width: 1200px;
      padding-bottom: 55px;
      background: #fff;
      margin: 0 auto;
      box-shadow: 0px 0px 10px 0px rgba(153, 153, 153, 0.5);

      h3 {
        height: 70px;
        line-height: 70px;
        color: #1A1A1A;
        border-bottom: 1px solid #E3E4E5;
        font-size: 18px;
        padding: 0 30px;
      }

      .line {
        width: 990px;
        height: 8px;
        background: #D0E0F9;
        margin: 70px auto;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        border-radius: 4px;

        div {
          float: left;
          width: 33.33%;
          height: 8px;
          position: relative;
          text-align: center;

          span {
            width: 32px;
            height: 32px;
            line-height: 32px;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            background: #D0E0F8;
            color: #09224B;
            font-size: 20px;
            position: absolute;
            left: 50%;
            margin-left: -16px;
            top: -12px;
          }
        }

        div.active {
          background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
          background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: linear-gradient(to bottom, #579EFA, #4F74F4);

          span {
            background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
            background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: linear-gradient(to bottom, #579EFA, #4F74F4);
            color: #fff;
          }
        }

        div:nth-child(1) {
          -webkit-border-radius: 4px 0 0 4px;
          -moz-border-radius: 4px 0 0 4px;
          border-radius: 4px 0 0 4px;
        }

        div:nth-child(3) {
          -webkit-border-radius: 0px 4px 4px 0;
          -moz-border-radius: 0px 4px 4px 0;
          border-radius: 0px 4px 4px 0;
        }
      }

      .evaTitle {
        font-size: 20px;
        color: #1A1A1A;
        text-align: center;
        margin-bottom: 40px;
      }

      .part1 {
        .row {
          width: 420px;
          margin: 0 auto 15px;

          .name {
            float: left;
            margin-right: 15px;
            color: #1A1A1A;
            line-height: 42px;
          }

          .textInp {
            float: left;
            width: 338px;
            height: 40px;
            line-height: 40px;
            border: 1px solid #DCDFE6;
            text-indent: 15px;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;
          }

          .selectBox {
            float: left;
            width: 130px;
          }

          .unit {
            line-height: 42px;
            margin-left: 8px;
          }

          .provice {
            margin-right: 28px;
          }
        }
      }

      .part2 {
        .choiceBox {
          width: 1000px;
          margin: 0 auto 30px;

          span {
            float: left;
            width: 75px;
            height: 36px;
            line-height: 36px;
            border: 1px solid #E3E4E5;
            text-align: center;
            color: #333;
            border-radius: 3px;
            margin: 0 3px 16px;
          }
        }

        .budget {
          width: 410px;
          margin: 0 auto;

          .name {
            float: left;
            line-height: 40px;
            color: #1a1a1a;
          }

          .border {
            float: right;
            width: 320px;
            padding-right: 18px;
            height: 38px;
            line-height: 38px;
            border: 1px solid #E3E4E5;
            position: relative;
            border-radius: 2px;

            input {
              width: 280px;
              height: 38px;
              line-height: 38px;
              text-indent: 15px;
            }
          }
        }
      }

      .part3 {
        .budget {
          width: 410px;
          margin: 0 auto 13px;

          .name {
            float: left;
            line-height: 40px;
            color: #1a1a1a;
          }

          .border {
            float: right;
            width: 338px;
            height: 38px;
            line-height: 38px;
            border: 1px solid #E3E4E5;
            position: relative;
            border-radius: 2px;

            input {
              float: left;
              width: 100%;
              height: 38px;
              line-height: 38px;
              text-indent: 15px;
            }

            .testInp {
              width: 220px;
            }

            .getBtn {
              float: right;
              width: 115px;
              height: 18px;
              line-height: 18px;
              border-left: 1px solid #E3E4E5;
              color: #5389F7;
              text-align: center;
              margin-top: 11px;
              cursor: pointer;
            }
            .imgCode{
              float: right;
              width: 115px;
              height: 38px;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .btns{
    text-align: center;
    margin-top: 60px;
    .el-button{
      width: 200px;
    }
  }

  .choiceBox {
    width: 1000px;
    margin: 0 auto 30px;


  }
</style>
<style >
  .el-checkbox-button {
    border-radius: 3px;
    margin: 0 10px 16px;
  }

  .el-checkbox-group .el-checkbox-button .el-checkbox-button__inner {
    border: 1px solid #DCDFE6;
    border-radius: 5px;
    box-shadow: 0 0 0 0 #8cc5ff;
  }
</style>



