import httpUtils from 'config/index'

class avaluateService {
  /**
   * 获取行业分类
   * @returns {*}
   */
  getCate() {
    return httpUtils.get("/index/category/get_cate_list");
  }
  /**
   * 获取城市
   * @returns {*}
   */
  getCity(params) {
    return httpUtils.get("/index/index/area",params);
  }

  /**
   * 提交信息
   */
  submitForm(params){
    return httpUtils.post("/index/assess/add_ass",params);
  }

  /**
   * 获取评估中的柱状图数据
   */
  getColumnData(params){
    return httpUtils.post("/index/assess/get_cate_avg_count",params);

  }

  /**
   * 获取评估信息
   */
  getEvaluate(params){
    return httpUtils.post("/index/assess/ass_all",params);
  }
}
export default new avaluateService()



